// Auto polyfill
import 'es6-object-assign/auto'
import 'es6-promise/auto'

// Import Vue
import Vue from 'vue'

// Import F7, F7-Vue
import Framework7 from 'framework7/framework7.esm.bundle.js'
import Framework7Vue from 'framework7-vue/framework7-vue.esm.bundle.js'

// Import F7 Styles
import 'framework7/css/framework7.css'

// Import App Custom Styles
import './assets/fonts/iconfont.css'
import './assets/styles/app.less'
import 'font-awesome/css/font-awesome.css'

// Import Routes
import Routes from './routes.js'

// Import App Component
import App from './app'

// Import Vuex store
import store from './store'
import {
  getLoginUser
} from './store/actions'

// import network framework
import service from './service/service'

// import i18n support
import i18n from './i18n'

// import store cache
import StoreCache from './utils/storeCache'

import {
  isAndroid
} from './utils/appFunc'

// Init F7 Vue Plugin
Framework7.use(Framework7Vue)

Vue.prototype.service = service

// Init App
new Vue({
  el: '#app',
  store,
  i18n,
  template: '<app/>',
  // Init Framework7 by passing parameters here
  data() {
    return {
      f7params: {
        id: 'com.byd.app', // App bundle ID
        name: 'BYD', // App name
        theme: isAndroid() ? 'md' : 'ios',
        // theme: 'ios',
        // App routes
        routes: Routes,
        dialog: {
          title: i18n.t('app.modal.title'),
          buttonOk: i18n.t('app.modal.button_ok'),
          buttonCancel: i18n.t('app.cancel')
        }
      }
    }
  },
  mounted() {
    this.$f7ready((f7) => {
      window.$$ = {
        alert: f7.dialog.alert,
        confirm: f7.dialog.confirm,
        prompt: f7.dialog.prompt,
        cache: new StoreCache('vuex')
      }
    })
  },
  methods: {},
  // Register App Component
  components: {
    app: App
  }
})

