<template>
  <f7-page>
    <f7-navbar :title="$store.state.title" back-link="返回"></f7-navbar>
    <f7-block>
       新增线索内容
    </f7-block>
  </f7-page>
</template>

<style lang="less">
.about-page {
  .logo {
    width: 90px;
    height: 90px;
    line-height: 90px;
    background-color: #fff;
    text-indent: -9999;
    border-radius: 5px;
    margin: 0 auto;
    border: 1px solid #dfdfdf;
    text-align: center;
    > img {
      width: 75px;
      height: 75px;
      vertical-align: middle;
    }
  }
  .app-name {
    text-align: center;
    color: #666;
    font-size: 20px;
    margin-top: 15px;
  }
  .detail {
    text-align: center;
    color: #666;
    > p {
      margin: 6px 0;
    }
  }
  .copyright {
    position: absolute;
    left: 0;
    text-align: center;
    bottom: 0;
    font-size: 10px;
    color: #9c9c9c;
    width: 100%;
    padding: 0;
  }
}
</style>

<script>
export default {
  data(){
    return {
      title:''
    }
  },
  mounted() {
    this.$nextTick(()=>{
      this.title = this.$f7.view.current.router.currentRoute.route.meta.title
    })
  }
};
</script>



