<template>
<f7-page id="homeView" class="home-view">
  <basic-view></basic-view>
  <div gutter class="row">
    <f7-col width="50">
      <f7-link href="clue/new" class="grid-cell">
        <f7-icon size="36" color="black" icon="fa fa-user-plus"></f7-icon>
        <div class="item-title">新增线索</div>
      </f7-link>
    </f7-col>
    <f7-col width="50">
      <f7-link href="/clue/edit" class="grid-cell">
        <f7-icon size="36" color="black" icon="fa fa-file-text-o"></f7-icon>
        <div class="item-title">线索维护</div>
      </f7-link>
    </f7-col>
    <f7-col width="50">
      <f7-link href="/clue/new" class="grid-cell">
        <f7-icon size="36" color="black" icon="fa fa-user-times"></f7-icon>
        <div class="item-title">战败审核</div>
      </f7-link>
    </f7-col>
    <f7-col width="50">
      <f7-link href="/clue/new" class="grid-cell">
        <f7-icon size="36" color="black " icon="fa fa-vcard-o"></f7-icon>
        <div class="item-title">线索转档案</div>
      </f7-link>
    </f7-col>
  </div>
</f7-page>
</template>

<script>
import axios from "axios";
import Card from "@/components/card";
import basicView from '../layout/userinfo'
import { mapState, mapActions } from "vuex";

export default {
  data() {
    return {
      refreshing: false,
      loadingMore: false,
      loadedEnd: false
    };
  },
  computed: {
    ...mapState({
      timeline: state => state.timeline
    })
  },
  mounted() {
  },
  methods: {
    ...mapActions(["initTimeline", "infiniteTimeline", "refreshTimeline"]),
    getTimeline() {
      // this.$f7.preloader.show()
      // axios.get('/timeline.json').then(res => {
      //   const timeline = res.data
      //   this.initTimeline(timeline)
      //   this.$f7.preloader.hide()
      // })
    },
    onRefresh() {
      if (this.refreshing) return false;

      this.refreshing = true;
      axios.get("/refresh_timeline.json").then(res => {
        if (parseInt(this.timeline[0].id) === 48) {
          this.$emit("show-tip");
        } else {
          const timeline = res.data;
          this.refreshTimeline(timeline);
        }
        this.refreshing = false;
        this.$f7.ptr.done();
      });
    },
    onInfiniteScroll() {
      if (this.loadingMore || this.loadedEnd) return false;

      this.loadingMore = true;
      axios.get("/more_timeline.json").then(res => {
        const id = parseInt(this.timeline[this.timeline.length - 1].id);
        if (id === 24) {
          this.loadedEnd = true;
          this.$f7.infiniteScroll.destroy("#homeView .infinite-scroll-content");
          this.$("#homeView .infinite-scroll-preloader").remove();
        } else {
          const timeline = res.data;
          this.infiniteTimeline(timeline);
        }
        this.loadingMore = false;
      });
    },
    routeToPost(data) {
      this.$f7router.navigate(`/post/?mid=${data.id}`);
    }
  },
  components: {
    Card,
    basicView
  }
};
</script>



