<template>
<f7-page id="homeView" class="home-view">
  <basic-view></basic-view>
  <div gutter class="row">
    <f7-col width="50">
      <f7-link href="/clue/new" class="grid-cell">
        <f7-icon size="36" color="black" icon="fa fa-user-plus"></f7-icon>
        <div class="item-title">新增人脉</div>
      </f7-link>
    </f7-col>
    <f7-col width="50">
      <f7-link href="/clue/new" class="grid-cell">
        <f7-icon size="36" color="black" icon="fa fa-file-text-o"></f7-icon>
        <div class="item-title">人脉维护</div>
      </f7-link>
    </f7-col>
  </div>
</f7-page>
</template>

<script>
import basicView from '../layout/userinfo'
export default {
  data() {
    return {
      refreshing: false,
      loadingMore: false,
      loadedEnd: false
    };
  },
  computed: {},
  mounted() {

  },
  methods: {
    onRefresh() {
      if (this.refreshing) return false;

      this.refreshing = true;
      this.freshing = false;
      this.$f7.ptr.done();
    },
    onInfiniteScroll() {},
    routeToPost(data) {
      this.$f7router.navigate(`/post/?mid=${data.id}`);
    }
  },
  components: {
    basicView
  }
};
</script>



