<template>
  <f7-page>
    <!-- <f7-navbar>
      <f7-nav-left></f7-nav-left>
      <f7-nav-title>{{navbarTitle}}</f7-nav-title>
      <f7-nav-right>
        <f7-link icon="iconfont icon-feedback3" icon-size="22" v-show="activedTab === 'clue'" @click="openPublisher"></f7-link>
      </f7-nav-right>
    </f7-navbar> -->
    <f7-toolbar tabbar :labels="true" :bottom-md="true">
      <f7-link icon="fa fa-user-plus" text="线索" tab-link="#clue" tab-link-active></f7-link>
      <f7-link icon="fa fa-address-book-o" text="人脉" tab-link="#connection"></f7-link>
      <f7-link icon="fa fa-users" text="客户" tab-link="#customer"></f7-link>
      <f7-link icon="fa fa-user-o" text="我的" tab-link="#me"></f7-link>
    </f7-toolbar>

    <f7-tabs>
      <f7-tab id="clue" tab-active @tab:show="tabActived('clue')">
        <clue-view @show-tip="showLoadResult"></clue-view>
      </f7-tab>
      <f7-tab id="connection" @tab:show="tabActived('connection')">
        <connection-view></connection-view>
      </f7-tab>
      <f7-tab id="customer" @tab:show="tabActived('customer')">
        <customer-view></customer-view>
      </f7-tab>
      <f7-tab id="me" @tab:show="tabActived('me')">
        <me-view></me-view>
      </f7-tab>
    </f7-tabs>

    <div class="load-result">{{$t('home.noNewestPost')}}</div>
  </f7-page>
</template>

<style lang="less" scoped>
.load-result {
  width: 100%;
  height: 30px;
  position: absolute;
  bottom: 50px;
  left: 0;
  background-color: #ff9500;
  color: #ffffff;
  z-index: 5001;
  text-align: center;
  line-height: 30px;
  opacity: 0;
}
.md {
  .load-result {
    bottom: 0;
  }
}
</style>

<script>
import clueView from "../clue/index";
import connectionView from "../connection/index";
import customerView from "../customer/index";
import meView from "../me/index";
import basicView from './userinfo'
import { mapActions } from "vuex";
import { isAndroid } from "@/utils/appFunc";

export default {
  data() {
    return {
      activedTab: "clue"
    };
  },
  computed: {
    navbarTitle() {
      switch (this.activedTab) {
        case "clue":
          return "线索";
        case "connection":
          return "人脉";
        case "customer":
          return "客户";
        case "me":
          return "我的";
      }
    },
    isAndroid() {
      return isAndroid();
    }
  },
  create() {},
  methods: {
    ...mapActions(["updatePopup"]),
    tabActived(tab) {
      this.activedTab = tab;
    },
    showLoadResult() {
      setTimeout(_ => {
        this.$$("div.load-result")
          .css("opacity", "1")
          .transition(1000);

        setTimeout(_ => {
          this.$$("div.load-result")
            .css("opacity", "0")
            .transition(1000);
        }, 2100);
      }, 400);
    },
    openPublisher() {
      this.updatePopup({
        key: "publisherOpened",
        value: true
      });
    }
  },
  components: {
    clueView,
    connectionView,
    customerView,
    meView,
    basicView
  }
};
</script>



