import mainPage from './pages/layout/main'
import aboutPage from './pages/about'
import feedbackPage from './pages/feedback'
import languagePage from './pages/language'
import profilePage from './pages/profile'
import messagePage from './pages/message'
import postPage from './pages/post'
import clueNew from './pages/clue/new'
import loginPage from './pages/login/index'

import store from './store'

import {
  setTitle
} from './store/actions'
import Cookies from 'js-cookie'

export default [{
    path: '/',
    name:'main',
    meta: {
      auth: true,
      component: mainPage
    },
    async: beforeRoute
  },
  {
    path: '/login/',
    component: loginPage
  },
  {
    path: '/profile/',
    component: profilePage
  },
  {
    path: '/language/',
    component: languagePage
  },
  {
    path: '/feedback/',
    component: feedbackPage
  },
  {
    path: '/about/',
    component: aboutPage
  },
  {
    path: '/message/',
    component: messagePage
  },
  {
    path: '/post/',
    component: postPage
  },
  {
    path: '/clue/new',
    meta: {
      title: '新建线索',
      auth: true,
      component: clueNew
    },
    async: beforeRoute
  },
  {
    path: '/clue/edit',
    meta: {
      title: '线索维护',
      auth: true,
      component: clueNew,
    },
    async: beforeRoute
  }
]

function beforeRoute(routeTo, routeFrom, resolve, reject) {
  if (!Cookies.get('user') && routeTo.route.meta.auth) {
    resolve({
      component: loginPage
    })
    return
  }
  store.commit('SET_TITLE', routeTo.route.meta.title)
  resolve({component: routeTo.route.meta.component})
}



